import Vue from 'vue'
import Router from 'vue-router'
import setCompany from '@/pages/setCompany'
import setOwner from '@/pages/setOwner'
import foodSaList from '@/pages/foodSaList'
import setFoodSa from '@/pages/setFoodSa'
import foodSa from '@/pages/foodSa/foodSa'
import foodSaValidate from '@/pages/foodSaValidate'
import foodSaDetail from '@/pages/foodSa/foodSaDetail'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'setCompany',
      component: setCompany
    },
    {
      path: '/user',
      name: 'setOwner',
      component: setOwner
    },
    {
      path: '/user/foodSaList',
      name: 'foodSaList',
      component: foodSaList
    },
    {
      path: '/user/foodSaList/setFoodSa',
      name: 'setFoodSa',
      component: setFoodSa
    },
    {
      path: '/firstLogin',
      name: 'foodSaValidate',
      component: foodSaValidate
    },
    {
      path: '/foodSa',
      name: 'foodSa',
      component: foodSa,
      children: [
        {
          path: 'foodSaDetail',
          name: 'foodSaDetail',
          component: foodSaDetail
        }
      ]
    }
  ]
});

export default router



